.app__works {
  flex: 1;
  width: 100%;
  flex-direction: column;
}

.app__work-filter {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  margin-block: 4rem 2rem;
  margin-inline: auto;

  .app__work-filter-item {
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    background-color: #fff;
    color: #000;
    font-weight: 800;
    cursor: pointer;
    transition: ALL 0.3s ease;
    margin: 0.5rem;

    &:hover {
      background-color: var(--secondary-color);
      color: #fff;
    }

    @media screen and (min-width: 2000px) {
      padding: 1rem 2rem;
      border-radius: 0.85rem;
    }
  }

  .item-active {
    background-color: var(--secondary-color);
    color: #fff;
  }
}

.app__work-portfolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  .app__work-item {
    width: 400px;
    flex-direction: column;
    margin: 2rem;
    padding: 1rem;
    border-radius: 0.5rem;
    background-color: var(--primary-color);
    color: var(--text-base);

    cursor: pointer;
    transition: ALL 0.3s ease;

    &:hover {
      box-shadow: 0 0 25px var(--box-shadow-color);
    }

    @media screen and (min-width: 2000px) {
      width: 470px;
      padding: 1.25rem;
      border-radius: 0.75rem;
    }

    // @media screen and (max-width: 300px) {
    //   width: 100px;
    //   margin: 1rem;
    // }

    @media screen and (max-width: 600px) {
      width: 90%;
    }
  }
}

.app__work-img {
  width: 100%;
  height: 230px;

  position: relative;

  img {
    width: 100%;
    height: 100%;

    border-radius: 0.5rem;
    object-fit: cover;
  }

  @media screen and (min-width: 2000px) {
    height: 350px;
  }
}

.app__work-hover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);

  border-radius: 0.5rem;
  opacity: 0;
  transition: all 0.3 ease;

  div {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;

    margin: 1rem;
    font-family: var(--font-base);
    font-weight: 800;
    cursor: pointer;
    transition: all 0.3 ease;

    &:hover {
      svg {
        color: var(--accent-color);
      }
    }

    svg {
      width: 50%;
      height: 50%;
      color: rgba(255, 255, 255, 0.9);
      transition: all 0.3s ease-in-out;
    }
  }
}

.app__work-content {
  padding: 0.5rem;
  width: 100%;
  position: relative;
  flex-direction: column;

  h4 {
    margin-top: 1rem;
    line-height: 1.5;
  }

  h4,
  p {
    text-align: center;
  }

  .more-info-container {
    margin-block: 2rem 1rem;
    border-radius: 30px;
    background-color: var(--white-color);
    padding: 1rem 2rem;

    .more-info {
      color: var(--text-accent);
      text-decoration: none;
      font-size: 1rem;
      font-weight: 700;
    }
  }

  .app__work-tag {
    position: absolute;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    background-color: var(--primary-color);
    top: -25px;
  }
}
