#home {
  position: relative;
  background: var(--header-bg);
  background-size: cover;
  background-position: center;
  background-repeat: repeat;
  color: var(--text-base);

  // disable the default padding styles
  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;

  margin-top: 2rem;
  padding: 6rem 2rem 0rem;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;

  margin: 0 2rem;

  @media screen and (max-width: 1200px) {
    width: 100%;
    margin-right: 0rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  .badge-cmp,
  .tag-cmp {
    padding: 1rem 2rem;
    background: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;

    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 0.5rem;

    p {
      width: 100%;
      // text-transform: uppercase;
      text-align: right;
    }
  }

  span {
    margin-bottom: auto;
    font-size: 2rem;

    @media screen and (min-width: 2000px) {
      font-size: 4rem;
    }
  }

  @media screen and (max-width: 1200px) {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;

  div {
    width: 100px;
    height: 100px;
    border-radius: 50%;

    background: #3a85ff25;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border: 2px solid var(--accent-color);
    transition: all 0.6s ease-in-out;

    img {
      width: 60%;
      height: 60%;
    }
  }

  // circles with different height & width

  div:nth-child(1) {
    width: 100px;
    height: 100px;
    border-radius: 53% 47% 32% 68% / 31% 45% 55% 69%;

    &:hover {
      border-radius: 81% 19% 39% 61% / 69% 32% 68% 31%;
    }
  }

  div:nth-child(2) {
    margin: 1.75rem;
    width: 150px;
    height: 150px;
    border-radius: 81% 19% 39% 61% / 69% 32% 68% 31%;

    &:hover {
      border-radius: 24% 76% 39% 61% / 69% 81% 19% 31%;
    }
  }

  div:nth-child(3) {
    width: 70px;
    height: 70px;
    border-radius: 24% 76% 39% 61% / 69% 81% 19% 31%;

    &:hover {
      border-radius: 53% 47% 32% 68% / 31% 45% 55% 69%;
    }
  }

  div:nth-child(4) {
    width: 70px;
    height: 70px;
    border-radius: 78% 22% 89% 11% / 28% 44% 56% 72%;

    &:hover {
      border-radius: 81% 19% 39% 61% / 69% 32% 68% 31%;
    }
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(1) {
      width: 200px;
      height: 200px;
    }

    div:nth-child(2) {
      width: 400px;
      height: 400px;
    }

    div:nth-child(3) {
      width: 170px;
      height: 170px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;

    div {
      margin: 1rem;
    }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;

  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;
  // border: 1px solid red;

  img {
    // border-radius: 37% 63% 34% 66% / 76% 40% 60% 24% ;
    // background-color: var(--accent-color);
    // border: 1px solid white;
    width: 100%;
    // height: 100%;
    object-fit: contain;
    z-index: 1;
    margin: auto;
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    transition: rotate 0.5s ease-in-out;
  }

  &:hover {
    .overlay_circle {
      rotate: -130deg;
    }
  }

  @media screen and (max-width: 1200px) {
    margin: 2rem 0;
  }
}
